  /**App-header  begin*/
.App-header {
  border: 2px solid black;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
  background-image: url(/hernalytics/static/media/Image10.d536f86e.png);
  background-size: cover;
}



  /**topnav begin*/
.topnav {
    overflow: hidden;
    border: 2px solid none;
    position: inherit;
    margin-top: -25.5%;
  }
  
  
  /**topnav a  begin*/
  .topnav a {
    float: left;
    display: block;
    color: white;
    font-weight:600;
    text-align: center;
    padding: 18px 27px;
    text-decoration: none;
    font-size: 17px;
  }
  

  
  .topnav a.active {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 12px 32px;
    /* material-theme/source/secondary */
    background: #E5B805;
    border-radius: 4px;
  }
  
  
  /**topnav .icon  begin*/
  .topnav .icon {
    display: none;
  }

  
  /**.App-link begin*/
  .App-link {
    color: #61dafb;
  }
  
  
  /**keyframes animations begin*/
  @-webkit-keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  @keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }


  
  /**.App-logo begin*/
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }



  /**dropdown begin*/
  .dropdown {
    display:block;
    border: 2px solid none;
    background-color: none;
  }
  
  /**dropdown .dropdown-content begin*/
  .dropdown .dropdown-content {
    background-color: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border: 2px solid none;
    margin-right: -100px;
    display: none;
    margin-top: -1%;
    position: relative;
  }  

    /**.dropdown:hover .dropdown-content begin*/
  .dropdown:hover .dropdown-content {
    border: 2px solid blue;
    display: block;

  }

  /**dropdown .dropdown-content li begin*/
  .dropdown .dropdown-content li{
    color: black;
    list-style-type: none;
    text-align: left;
    border: 2px solid black;
    background-color: #f9f9f9;
    margin-left: -21%;
    padding: 3% 10%;
  }


  .content_container {
    border: 2px solid none;
    display: flex;
    text-align: left;
    color: white;
    flex-direction: column;
    margin-left: -50vh;

  }

  .content_details {
    border: 2px solid none;
    width: 70%;
  }

  .content_details p {
    font-size: small;
  }


  .content_button_container {
    border: 2px solid none;
    display: flex;
    width: 70%;
    justify-content: space-around;
  }

  .content_button {
    border: white 2px solid;
    padding: 1%;
    width: 240px;
    height: 23px;
    border-radius: 0.3rem;
    font-size: 16px;
    font-weight: 600;
  }

  .content_button a{
    display: flex;
    justify-content: center;
  }

  .sign-up {
    color: black;
    border: 2px solid #ECB700;
    background: #ECB700;
  }





  /**Media queries prefers-reduced-motion begin*/
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      -webkit-animation: App-logo-spin infinite 20s linear;
              animation: App-logo-spin infinite 20s linear;
    }
  }



  /**Media queries 600px begin*/
  
  @media screen and (max-width: 600px) {
    
    .topnav a:not(:first-child) {
      display: none;
    }

    .topnav a.icon {
      float: right;
      display: block;
    }

    .topnav.responsive {
      position: relative;
    }
    
    .topnav.responsive .icon {
      position: absolute;
      right: 0;
      top: 0;
    }
    .topnav.responsive a {
      float: none;
      display: block;
      text-align: 
      left;
    }

    .topnav {
      overflow: hidden;
      border: 2px solid none;
      background-color: #f9f9f9;
      position: relative;
      -webkit-transform: translateY(-270%);
              transform: translateY(-270%);
      width: 100%
    }

    .content_container {
      border: 2px solid none;
      display: flex;
      text-align: left;
      color: white;
      flex-direction: column;
      margin-left: 2%;
      margin-right: auto;
  
    }
  
    .content_details {
      border: 2px solid none;
      width: 80%;
    }
  
    .content_details p {
      font-size: small;
    }
  
  
    .content_button_container {
      border: 2px solid none;
      display: flex;
      width: 75%;
    }
  
    .content_button {
      border: 2px solid white;
      width: 300px;
      border-radius: 0.2rem;
      padding: 1% 0%;
    }
  
    .content_button a{
      display: flex;
      justify-content: center;
    }
  
    .sign-up {
      color: black;
      border: 2px solid #ECB700;
      background: #ECB700;
    }

    .get-election-update {
      margin-left: 1.5%;
    }

  }


  

  
